/*----- Project Container -----*/

@import url('../../assets/css/Global.css');

img {
    width: auto;
    height: 100%;
    -webkit-box-flex: 0.5;
        -ms-flex: 0.5;
            flex: 0.5;
    border-radius: 10px;
}

.project-info {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-flex: 1;
        -ms-flex: auto;
            flex: auto;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}

.project-info .project-name {
    color: var(--color-primary-text);
    font-size: 22.5px;
    letter-spacing: .5px;
    margin-bottom: 1%;
}

.project-info .project-type {
    color: var(--color-content);
    font-size: 16px;
    margin-bottom: 5%;
    letter-spacing: .5px;
}

.project-info .project-description {
    color: var(--color-secondary-text);
    font-size: 17px;
    line-height: 1.4;
    letter-spacing: .5px;
}

@media (max-width: 576px) {
    /* styles for small screens go here */

    img {
        width: 100%;
        height: auto;
    }

    .project-info {
        padding: 10% 10px 0 10px;
    }

    .project-info .project-type {
        font-size: 15px;
    }
    
    .project-info .project-name {
        font-size: 21px;
    }
    
    .project-info .project-description {
        font-size: 15px;
    }
}

@media (min-width: 577px) and (max-width: 992px) {
    /* styles for medium screens go here */

    img {
        width: 100%;
        height: auto;
    }

    .project-info {
        padding: 10% 10px 0 10px;
    }

    .project-info .project-type {
        font-size: 15px;
    }
    
    .project-info .project-name {
        font-size: 21px;
    }
    
    .project-info .project-description {
        font-size: 15px;
    }
}

@media (min-width: 993px) and (max-width: 1200px) {
    /* styles for large screens go here */

    .project-info .project-type {
        font-size: 15px;
    }
    
    .project-info .project-name {
        font-size: 21px;
    }
    
    .project-info .project-description {
        font-size: 15px;
    }
}
